.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

html {
  background-color: #282c34;
}

body {
  background-color: #333;
  color: rgb(139, 139, 139);
  font-family: Helvetica, Arial;
  font-size: 3vmin;
}

.hidden {
  display: none;
}

/** Top Nav Bar **/

.title {
  color: #eee;
  opacity: 1;
  z-index: 1039;
  margin: 8px;
  position: fixed;
  top: 0;
}

.title-login {
  font-size: 100px;
  position: static;
  display: flex;
  flex-flow: column;
  align-self: center;
}

.title-login::after {
  content: "Welcome, this is branch.out\AI help people find new music that's coming to a nearby stage soon.\ALet's find the the next concert you'll never forget";
  font-size: large;
  white-space: pre-wrap;
  margin-top: 10px;
}

.top-bar {
  background-color: #282c34;
  opacity: 0.6;
  border-bottom: 1px solid #aaa;
}

.top-right-btns {
  max-width: 35%;
}

.top-right-btns > .btn {
  font-size: 12px;
}

/** Footer **/
.bottom-bar {
  background-color: #282c34;
  opacity: 0.6;
  border-top: 1px solid #aaa; 
  font-size: small; 
}

.ccs-thank-you > a, .created-by > a {
  color: white;
}

/** Player **/
.player-wrapper {
  margin: 25px;
  padding: 25px;
  border-bottom: solid 1px #aaa;
}

/** Buttons **/

.btn {
  background-color: transparent;
  border-radius: 2em;
  border: 0.2em solid #1ecd97;
  color: #1ecd97;
  cursor: pointer;
  font-size: calc(8px + 1.5vmin);
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
  margin: 5px;
}

.btn:hover {
  background: #1ecd97;
  color: #333;
}

.btn-nohover:hover {
  background: transparent;
  color: white;
}

.btn-selected {
  background: #1ecd97;
  color: #333;
}

.btn-disabled {
  background-color: transparent;
  border-radius: 2em;
  border: 0.2em solid rgb(139, 139, 139);
  color: rgb(139, 139, 139);
  font-size: 2.5vmin;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
  margin: 5px;
}

.btn-disabled:hover {
  cursor: default;
}
.btn--login {
  margin: 0 auto;
}

.btn-logout {
  background-color: #282c34;
}

.btn-delete {
  left: -15px;
  color: #aaa;
  background: transparent;
  border: none;
  position: absolute;

}

.refresh-btn {
  font-size: 33px;
  color: #fff;
  background: none;
  border: none;
  width: 60px;
  height: 60px;
}

/** Background **/

.background {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  filter: blur(8em) opacity(0.6);
  position: absolute;
}

.main-wrapper {
  align-items: center;
  display: flex;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  position: relative;
  width: 80%;
  z-index: 1;
}

.player-wrapper {
  display: flex;
  align-items: center;
}

.container {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
}

.main-container {
  flex: 1;
}

/** Branch **/
.branch-img-preview-btn {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid #aaa;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  position: relative;
}

.branch-img-preview {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

/** Limbs **/
.limb {
  border-top: 1px solid #aaa;
  height: 42px;
  display: flex;
  align-items: center;
  position: relative;
}
  
.limb:hover {
  background: #aaa;
}
  
.limb:last-child {
  border-bottom: 1px solid #aaa;
}

.limbs {
  display: flex;
  flex-flow: column wrap;
  margin-bottom: 15px;
  width: 90vw;
  font-size: calc(6px + 1.5vmin);
}

.limb > div > a {
  color: white;
}